* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
}

h1 {
  font-weight: 700;
  color: rgb(80, 80, 80);
  font-size: 3rem;
}

h2 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

h3 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

p {
  font-size: 1.15rem;
  color: rgb(100, 100, 100);
}

.link-set .icon {
  transition: all 150ms ease-in-out;
}
.link-set .icon:hover {
  scale: 1.25;
}

button {
  border: 1px solid black;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1rem;
  font-weight: 400;
  cursor: pointer;
  transition: all 150ms ease-in-out;
  background-color: rgb(240, 240, 240);
  color: black;
}
button:hover {
  background-color: black;
  color: white;
}

header {
  display: flex;
  position: fixed;
  z-index: 1000;
  width: 100%;
  align-items: center;
  flex-direction: column;
}

/*
font-family: 'Cardo', serif;
