.img-head-container {
  display: flex;
  align-items: center;
  min-height: 600px;
  width: 100%;
  background-position: center right;
  background-size: 1100px;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: rgba(0, 128, 128, 0.3);
